html {
  height: 100vh;
  width: 100vw;
}

#root {
  height: 100vh;
  width: 100vw;
}

#main {
  height: 100vh;
  width: 100vw;
  background-color: cornflowerblue;
}

h1 {
  font-family: 'Bangers', cursive;
  font-size: 3em;
}

#quote-box {
  padding: 2em;
  border-radius: 1em;
  min-height: 250px;
}

#text {
  font-size: 1.3em;
  font-weight: bold;
}

#author {
  font-size: 0.9em;
}

button {
  width: 15em;
  height: 3em;
  margin-top: 2em;
}

a {
  font-size: 2em;
  margin: 0.4em;
}
